<template><section><h1>RectangleGeometry, RectangleOutlineGeometry</h1>
<ul>
<li>The <code v-pre>vc-geometry-rectangle</code> component is used to load a geometry that describes a cartographic rectangle on an ellipsoid centered at the origin.</li>
<li>The <code v-pre>vc-geometry-outline-rectangle</code> component is used to load a geometry that describes the outline of a a cartographic rectangle on an ellipsoid centered at the origin.</li>
<li>It needs to be used as a sub-component of <code v-pre>vc-instance-geometry</code>, can be mounted on <code v-pre>vc-primitive</code> or <code v-pre>vc-primitive-ground</code>.</li>
</ul>
<h2>Example</h2>
<h3>Load RectangleGeometry</h3>
<h4>preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-primitive :appearance="appearance">
          <vc-instance-geometry>
            <vc-geometry-rectangle ref="rectangle" :rectangle="rectangle" :vertexFormat="vertexFormat"></vc-geometry-rectangle>
          </vc-instance-geometry>
        </vc-primitive>
        <vc-primitive :appearance="appearanceOutline">
          <vc-instance-geometry :attributes="attributes">
            <vc-geometry-outline-rectangle ref="rectangleOutline" :rectangle="rectangleOutline"></vc-geometry-outline-rectangle>
          </vc-instance-geometry>
        </vc-primitive>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
