<h2>Instance Properties</h2>
<h3><code v-pre>vc-geometry-rectangle</code></h3>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>rectangle</td>
<td>Object</td>
<td></td>
<td><code v-pre>required</code> A cartographic rectangle with north, south, east and west properties in radians. <strong>structure: { west: number, south: number, east: number, north: number }</strong></td>
</tr>
<tr>
<td>vertexFormat</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> The vertex attributes to be computed.</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> The ellipsoid on which the rectangle lies.</td>
</tr>
<tr>
<td>granularity</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> The distance, in radians, between each latitude and longitude. Determines the number of positions in the buffer.</td>
</tr>
<tr>
<td>height</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> The distance in meters between the rectangle and the ellipsoid surface.</td>
</tr>
<tr>
<td>rotation</td>
<td>Number</td>
<td><code v-pre>0.0</code></td>
<td><code v-pre>optional</code> The rotation of the rectangle, in radians. A positive rotation is counter-clockwise.</td>
</tr>
<tr>
<td>stRotation</td>
<td>Number</td>
<td><code v-pre>0.0</code></td>
<td><code v-pre>optional</code> The rotation of the texture coordinates, in radians. A positive rotation is counter-clockwise.</td>
</tr>
<tr>
<td>extrudedHeight</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> The distance in meters between the rectangle's extruded face and the ellipsoid surface.</td>
</tr>
</tbody>
</table>
